	module.exports = {"data":{"profile":{"id":"dad848a0-3e8b-5bc4-89b2-322226f31973","content":"<p><div class=\"content-block  text-center\"><p>Peter Fedele has extensive teaching and performance experience which spans over two decades. He is an accomplished guitarist who performs at wedding ceremonies, cocktail receptions and other special occasions. He will personalize your event by providing a wide choice of music selections in his repertoire and will also learn songs by request from Jack Johnson to Bob Marley to the Gipsy Kings.</p></div></p>\n<p><div class=\"content-block  text-center\"><p>He is one of the finest jazz guitarists in the New England area whose individual style combines bebop, rock and funk creating unique harmonic improvisations and compositions. Coupled with his virtuosity in jazz, Peter is equally comfortable with classical music and Spanish/Flamenco guitar.</p></div></p>\n<p><div class=\"content-block  text-center\"><p>In addition to private functions, he also performs in the New England area&#8217;s finest hotels, restaurants and clubs. His work ranges from solo to big band and this versatility makes him a highly sought after talent. He has collaborated with jazz greats such as Bob Gullotti, Rob Sheps and Herb Pomeroy.</p></div></p>\n<p><div class=\"content-block  text-center\"><p>As an educator Peter is affiliated with the New England Conservatory of Music, Berklee College of Music, Waring School and Landmark School as an accompanist and instructor. Peter teaches all styles, abilities and age levels. He welcomes advanced students who are preparing for college and/or a career in music. He also teaches reading, music theory, ear training, improvisation and composition.</p></div></p>\n<p><div class=\"content-block  text-center\"><p>As a writer he is prolific with numerous original compositions to his credit.</p></div></p>\n<p><div class=\"content-block  text-center\"><p>He has studied music and guitar for twenty five years and is an alumnus of Berklee College of Music. Peter studied with world renowned pianist and music educator Charlie Banacos for over two decades. This man is the real deal. Listen to his music on our web site and you will hear for yourself.</p></div></p>\n","title":"Peter Fedele","acf":{"title":"Classical/Jazz Guitar","video":null,"facebook":"","instagram":"","testimonials":""},"image":{"localFile":{"childImageSharp":{"thumbnail":{"base64":"data:image/jpeg;base64,","aspectRatio":0.7168458781362007,"src":"/static/f3fa8dae1ce2e2f695c27fe036ae0519-c1f3f7384a342ee079732ccd039ea115-06ad8.jpg","srcSet":"/static/f3fa8dae1ce2e2f695c27fe036ae0519-c1f3f7384a342ee079732ccd039ea115-a24bd.jpg 110w,\n/static/f3fa8dae1ce2e2f695c27fe036ae0519-c1f3f7384a342ee079732ccd039ea115-06ad8.jpg 200w","sizes":"(max-width: 200px) 100vw, 200px","originalImg":"/static/f3fa8dae1ce2e2f695c27fe036ae0519-c1f3f7384a342ee079732ccd039ea115-06ad8.jpg"},"full":{"src":"/static/f3fa8dae1ce2e2f695c27fe036ae0519-c1f3f7384a342ee079732ccd039ea115-edd6d.jpg","height":279,"width":200,"srcSet":"/static/f3fa8dae1ce2e2f695c27fe036ae0519-c1f3f7384a342ee079732ccd039ea115-edd6d.jpg 1x"}}}},"yoast":{"title":"","canonical":"","metaDescription":"","linkdex":"","metakeywords":"","noIndex":"","noFollow":"","meta_robots_adv":"","metaKeywords":"","redirect":"","ogTitle":"","ogDescription":"","ogImage":"","twitterTitle":"","twitterDescription":"","twitterImage":""}},"site":{"id":"Site","siteMeta":{"title":"C-Zone Entertainment","subtitle":"Premiere DJs & Musicians | Servicing Greater Boston & New England | 617.320.0723"}}},"pathContext":{"id":"dad848a0-3e8b-5bc4-89b2-322226f31973"}}

